export default {
    name: "app-hotspot",
    props: {
        hotspot: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {}
    },
    onLoad() {
    },
    computed: {
        aa() {
            if (this.hotspot) {
                return `left:${this.hotspot.left}rpx;top:${this.hotspot.top}rpx;width:${this.hotspot.width}rpx;height:${this.hotspot.height}rpx;`;
            } else {
                return ``;
            }
        }
    }
};
